header {
    position: absolute;
    width: 100%;
    z-index: 1;
    background: $colorLightGrey;
    backdrop-filter: blur(8px);
    transition: transform 0.3s;
    will-change: transform;

    &.scroll {
        position: fixed;
    }

    &.scroll-down {
        transform: translate3d(0, -100%, 0);
    }

    .container_big {
        max-width: min(calc(1720px - 30px));
        padding: 15px;
        margin-inline: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .logo {
            font-size: 32px;
            font-weight: 900;
            color: $colorGrey;
            transition: $transition;
            will-change: transform;

            &::after {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                display: block;
                width: 100%;
                height: 0;
                background: $colorGrey;
                transition: 0.4s cubic-bezier(0.075, 0.5, 0, 1) 0.5s;
                will-change: transform;
            }
        }

        nav {
            display: flex;
            align-items: center;

            .links {
                display: flex;
                margin-right: 40px;
                gap: 32px;
                a {
                    font-size: 16px;
                    color: $colorGrey;
                    position: relative;

                    &::after {
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 0;
                        display: block;
                        width: 0;
                        height: 100%;
                        background: $colorGrey;
                        transition: 0.4s cubic-bezier(0.075, 0.5, 0, 1) 0.5s;
                        will-change: transform;
                    }
                }
            }
            .switch {
                margin-right: 20px;
                position: relative;
                width: 44px;
                height: 24px;
                background: $colorLightBlue;
                border-radius: 50px;
                padding: 2px;
                position: relative;

                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    display: block;
                    width: 0;
                    height: 100%;
                    background: $colorGrey;
                    transition: 0.4s cubic-bezier(0.075, 0.5, 0, 1) 0.5s;
                    will-change: transform;
                }

                input {
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    position: absolute;
                    z-index: 1;
                    left: 0;
                    top: 0;
                    cursor: pointer;

                    &:checked ~ label {
                        transform: translateX(20px);
                        background: $colorBlue url(../img/moon.svg) center no-repeat;
                    }
                }
                label {
                    display: inline-block;
                    height: 20px;
                    width: 20px;
                    border-radius: 50%;
                    background: $colorBlue url(../img/sun.svg) center no-repeat;
                    transition: $transition;
                    will-change: transform;
                }
            }
            .language {
                cursor: pointer;
                position: relative;
                display: flex;
                align-items: center;
                gap: 3px;
                font-weight: 500;

                svg {
                    transition: $transition;
                    fill: $colorGrey;
                }

                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    display: block;
                    width: 0;
                    height: 100%;
                    background: $colorGrey;
                    transition: 0.4s cubic-bezier(0.075, 0.5, 0, 1) 0.5s;
                    will-change: transform;
                }

                &.animation {
                    svg {
                        animation: animationRotate $transition;
                    }
                }
            }
        }
    }
}

@keyframes animationRotate {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(600deg);
    }
}

.animation header .container_big nav .links a::after {
    width: 100%;
}

.animation header .container_big nav .switch::after {
    width: 100%;
}

.animation header .container_big nav .language::after {
    width: 100%;
}
.animation header .container_big .logo::after {
    height: 100%;
}
